*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    font-size: 1.6rem;
    font-family: 'Inter', sans-serif;
}

body,
html{
    width: 100%;
    height: 100%;
    font-size: 62.5%;
    background: radial-gradient(circle at right, $clr-purple 5%, $clr-black-dark 70%);
    background-size: cover;
    background-repeat: no-repeat;
    scroll-behavior: smooth;
}

img{
    max-width: 100%;
}

button{
    cursor: pointer;
}